zvezda = '*'
function zadanie6() {
    var h = document.getElementById("zad61")
    var w = document.getElementById("zad62")
    var height = h.value
    var width = w.value
    answer = document.getElementById("answer1")
    voskl = document.getElementById("f")
    // cop = document.getElementsByClassName('copy')
    elems = document.querySelectorAll('.copy');
    if (height <= 0 & width <= 0) {
        answer.classList.toggle('rot')
        voskl.classList.toggle('morg')
        voskl.classList.toggle('prolitariat')
    }
    else {
        // alert(cop)
        // while (cop.lenght) cop[0].parentNode.removeChild(cop[0])
        for (i = 0; i < elems.length; i++) {
            elems[i].parentNode.removeChild(elems[i]);
        }
        for (let i = 0; i < height; i++) answer.insertAdjacentHTML("beforeend", `<p class="copy">${zvezda.repeat(width)}</p>`)
    }
}
function dopzad() {
    answer = document.getElementById("answer1")
    document.answer.classList.toggle('rot')
}
var button = document.getElementById('button1')
button.addEventListener("click", zadanie6)
